import React from 'react';
import {Link} from 'react-router-dom';

export default props => {
	return(
		<Link to={props.queryParam}><div className="filter-item" 
			onClick={(e)=> {
				let selEl = document.querySelectorAll('.active');
				if(selEl[0]){
					selEl[0].classList.remove('active');
				}
				e.target.className = `${e.target.className} active`;
			}}>
			{props.value}
			</div>
		</Link>
	)
	
}