import React from 'react';
import RocketCard from './RocketCard';

export default props => {
	let rocketList = [];
	if(props.data.length === 0){
		rocketList = <div style={{display:"grid",gridColumn:"1/8",textAlign:"center",background:"#fff",alignItems:"center"}}>No Records Found</div>
	}
	else{
		rocketList = props.data.map(rec => {
			return <RocketCard key={rec.flight_number} info={rec}/>
		});
	}
	return <div className="rocket-grid">{rocketList}</div>;
}